@import "@material/react-card/index.scss";
.roundcard {
  @include mdc-card-shape-radius(8px, false);
}
.greencard {
  @include mdc-card-fill-color(mediumspringgreen);
}
.bluecard {
  @include mdc-card-fill-color(rgb(19, 226, 233));
}
.pinkcard {
  @include mdc-card-fill-color(deeppink);
}
.readcardmedia {
  @include mdc-card-media-aspect-ratio(1, 1.2)
}